@use "../abstract" as *;

.example {
  position: relative;
  z-index: 1;
  background-color: $milk;
  min-height: 659px;

  @media (min-width: $tablet-width) {
    background-color: transparent;
  }
}

.example__wrapper {
  @media (min-width: $desktop-width) {
    display: flex;
  }
}

.example--color::after {
  @media (min-width: $tablet-width) {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 35%;
    background: linear-gradient(194deg, #eaeaea, $white);
    z-index: -1;
  }
}

.example__desc-container {
  @media (min-width: $tablet-width) {
    width: 676px;
    padding-top: 27px;
  }

  @media (min-width: $desktop-width) {
    width: 436px;
    padding-top: 153px;
    padding-left: 29px;
  }
}

.example__title {
  font-family: $oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 111%;
  color: $black;
  margin: 0;
  padding: 0;
  grid-column: 1/3;

  @media (min-width: $tablet-width) {
    line-height: 100%;
  }
}

.example__title--main {
  font-size: 36px;
  line-height: 75%;
  padding-bottom: 40px;
  padding-top: 30px;
  padding-left: 0;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 67%;
    padding-top: 73px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
    padding-top: 107px;
    padding-left: 30px;
  }
}

.example__main-description {
  margin: 0;
  font-family: $arial;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  color: $grey;
  padding-bottom: 20px;
  padding-top: 7px;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 150%;
  }

  @media (min-width: $desktop-width) {
    padding-top: 5px;
  }
}

.example__description {
  font-family: $arial;
  font-weight: 400;
  font-size: 12px;
  line-height: 100%;
  width: 75px;
  text-align: center;
  padding-top: 45px;
  padding-left: 23px;
  padding-right: 36px;
  margin: 0;

  @media (min-width: $tablet-width) {
    width: 142px;
    font-size: 14px;
    padding-top: 65px;
    padding-left: 9px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 62px;
    padding-left: 10px;
    padding-right: 20px;
  }
}

.example__span {
  position: absolute;
  font-family: $oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 100%;
  text-transform: uppercase;
  color: $black;
  left: 42px;
  top: 14px;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 123%;
    left: 57px;
  }

  @media (min-width: $desktop-width) {
    left: 58px;
  }
}

.example__space {
  @media (min-width: $tablet-width) {
    display: block;
    margin-top: 9px;
  }
}

.example__span--days {
  left: 19px;

  @media (min-width: $tablet-width) {
    left: 30px;
  }
}

.example__info {
  position: relative;
  width: 124px;
  background-image: url("../img/slider-border.svg");
  background-repeat: no-repeat;


  @media (min-width: $tablet-width) {
    position: relative;
    width: 162px;
    background-image: url("../img/slider-border-tablet.svg");
    min-height: 72px;
  }
}

.example__info--days {
  @media (min-width: $tablet-width) {
    margin-left: 10px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 65px;
  }
}

.example__info--weight {
  @media (min-width: $desktop-width) {
    margin-left: 29px;
  }
}

