.example__little-title {
  font-family: $oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
  color: $black;
  margin: 0;
  padding: 0;
  padding-top: 20px;
  padding-left: 45px;
  min-width: 188px;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: $tablet-width) {
    line-height: 100%;
    font-size: 20px;
    padding-left: 119px;
    padding-top: 10px;
    text-align: right;
  }

  @media (min-width: $desktop-width) {
    padding-left: 0;
  }
}
